import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { ActivatedRoute } from '@angular/router'; 
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.css']
})
export class RegistrationComponent implements OnInit {

  uType: string;
  uContact: string;
  isProviderValid : Boolean = false;
  isWorkerValid : Boolean = false;

  constructor(
    private route: ActivatedRoute,
    private _dataService: DataService
  ) { }

  ngOnInit() {
    this.route.queryParams
    .subscribe(params =>{
      if (params["utype"] == "p")
      {
        this.uType = "I am looking support for";
        this.uContact = "Emergency Contact Details";
        this.isProviderValid = true;
      }
      else
      {
        this.uType = "I want to become a support worker";
        this.uContact = "Referee Contact Details";
        this.isWorkerValid = true;
      }
    });

    this._dataService.editMessage("Create An Account");
  }

  submitUserData()
  {
    alert('Registration Completed successfully');
  }

}



